export default {
	data() {
		return {
			WindowHeight_one_five:this.WindowHeight_one_five,
			StatusAddNav:this.StatusAddNav,
			StatusBarHeight:this.StatusBarHeight,
			NavigationBar:this.NavigationBar,
			// 是否登录标志
			isLoginFlag: false,
			/******************/
			// banner
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 1000,
			userInfo:{
				collect_merchant:0,
				collect_product:0,
				phone:"",
				avatar:"",
			},
			jumpFlag:true,
		};
	},
 
	onShow() {
		if(this.jumpFlag){
			this.isLogin();
			this.jumpFlag=false;
		}
		this.getUserInfo();
	},
	methods: {
		//获取用户的信息
		getUserInfo(){
			let that=this;
			that.basePost(
				that.U({ c: 'user_api', a: 'my'}),
				{},
				function(res) {
					that.userInfo=res.data
					that.isLoginFlag=true;
				},
				function(res) {
					that.isLoginFlag=false;
					that.userInfo=''
				}
			);
		},
		//跳转登录页面
		toLogin(){
			uni.navigateTo({
				url:"/pages/login/pwd_login/pwd_login"
			})
		},
		edit() {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/user/info_edit/info_edit'
			});
		},
		news(e) {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/user/my_news/my_news'
			});
		},
		mycol(e) {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/shop/my_collection/my_collection'
			});
		},
		shopcol(e) {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/shop/shop_collection/shop_collection'
			});
		},
		order(e) {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/user/my_order/my_order?index='+e
			});
		},
		coupon(e) {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/user/my_coupon/my_coupon'
			});
		},
		info(e) {
			if(!this.isLogin())return;
			uni.navigateTo({
				url: '/pages/user/my_info/my_info'
			});
		},
		navToSubscribe(){
			if(!this.isLogin())return;
			uni.switchTab({
				url:'/pages/subscribe/subscribe'
			})
		},
		navToPlantingRules(){
			if(!this.isLogin())return;
			uni.navigateTo({
				url:'/pages/user/planting_rules/planting_rules'
			})
		},
		navToSafeCheck(){
			if(!this.isLogin())return;
			uni.navigateTo({
				url:'/pages/user/safe_check/safe_check'
			})
		}
		
	}
};
